  // Informer to stop using observables after component is destroyed
  private ngUnsubscribe: Subject<void> = new Subject<void>();

  constructor(private resizeService: ResizeService) {
  }

  ngOnInit() {
    this.gradientRadarChart = echarts.init(this.gradientRadarChartElementRef.nativeElement);
    this.gradientRadarChart.setOption(this.gradientRadarChartOption);

    this.areaRadarChart = echarts.init(this.areaRadarChartElementRef.nativeElement);
    this.areaRadarChart.setOption(this.areaRadarChartOption);

    this.lineRadarChart = echarts.init(this.lineRadarChartElementRef.nativeElement);
    this.lineRadarChart.setOption(this.lineRadarChartOption);

    this.resizeService.resizeInformer$.pipe(takeUntil(this.ngUnsubscribe)).subscribe(() => {
      this.gradientRadarChart.resize();
      this.areaRadarChart.resize();
      this.lineRadarChart.resize();
    })
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

